export default {
	data() {
		return {
			modalName: null,
			gridCol: 3,
			gridBorder: false,
			menuBorder: false,
			menuArrow: true,
			menuCard: false,
			skin: false,
			listTouchStart: 0,
			listTouchDirection: null,
		};
	},
	methods: {
		ontrueGetList(){
			uni.setNavigationBarTitle({
			    title: '地图'
			});
			uni.setNavigationBarColor({
			    frontColor: '#000000',
			    backgroundColor: '#ffffff'
			})
			console.log("加载了第五个页面，可以把网络请求放这里")
		},
		lower(){
			uni.showToast({
				title:'scroll-view的加载更多'
			})
			console.log("分页数据可以放这里~~~~~~嘿嘿")
		},
		MenuArrow(e) {
			this.menuArrow = e.detail.value
		},
		MenuCard(e) {
			this.menuCard = e.detail.value
		},
		navTo(url){
			uni.navigateTo({
				url:url
			})
		}
	}
}
